import React, { useState, useRef, useEffect,createContext,useContext} from 'react';
import Login from './login';
import Sreenshot from './ScreenShot.js';
import {
  Card, Button
} from "reactstrap";
const GlobalValueContext = createContext();

// Provider component
function GlobalValueProvider({ children }) {
  const [globalValue, setGlobalValue] = useState(null);

  return (
    <GlobalValueContext.Provider value={[globalValue, setGlobalValue]}>
      {children}
    </GlobalValueContext.Provider>
  );
}

function App() {
	
  return (

<Card className="my-4" style={{ textAlign: 'center', padding: '20px' }}
  
>	
	<h1>Get my Cover Letter</h1>
	
	
     <Login />
	  </Card>
    
	
  );
}

export default App;
